@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  @font-face {
    font-family: "SF Pro Display";
    src: url("../fonts/SF-Pro-Display-Regular.eot");
    src: local("☺"), url("../fonts/SF-Pro-Display-Regular.woff") format("woff"),
      url("../fonts/SF-Pro-Display-Regular.ttf") format("truetype"),
      url("../fonts/SF-Pro-Display-Regular.svg") format("svg");
    font-weight: normal;
    font-style: normal;
  }

  @font-face {
    font-family: "SF Pro Display";
    src: url("../fonts/SF-Pro-Display-Semibold.eot");
    src: local("☺"), url("../fonts/SF-Pro-Display-Semibold.woff") format("woff"),
      url("../fonts/SF-Pro-Display-Semibold.ttf") format("truetype"),
      url("../fonts/SF-Pro-Display-Semibold.svg") format("svg");
    font-weight: 500;
    font-style: normal;
  }

  @font-face {
    font-family: "SF Pro Display";
    src: url("../fonts/SF-Pro-Display-Semibold.eot");
    src: local("☺"), url("../fonts/SF-Pro-Display-Semibold.woff") format("woff"),
      url("../fonts/SF-Pro-Display-Semibold.ttf") format("truetype"),
      url("../fonts/SF-Pro-Display-Semibold.svg") format("svg");
    font-weight: 600;
    font-style: normal;
  }

  @font-face {
    font-family: "SF Pro Display";
    src: url("../fonts/FontsFree-Net-SFProDisplay-Bold.eot");
    src: local("☺"),
      url("../fonts/FontsFree-Net-SFProDisplay-Bold.woff") format("woff"),
      url("../fonts/FontsFree-Net-SFProDisplay-Bold.ttf") format("truetype"),
      url("../fonts/FontsFree-Net-SFProDisplay-Bold.svg") format("svg");
    font-weight: 700;
    font-style: normal;
  }

  .tracking-lesstight {
    letter-spacing: -0.01em;
  }

  button.border[disabled] {
    filter: grayscale(1) opacity(0.75);
    cursor: auto;
  }

  input[aria-invalid]:not(:focus),
  select[aria-invalid]:not(:focus) {
    @apply border-red-700;
  }

  input[name][disabled] {
    opacity: 0.5;
  }
}

.lno-buttonish-radio:checked + * {
  @apply bg-lean-orange;
}
.lno-buttonish-radio:focus + * {
  @apply ring;
}

.lno-dialog-content {
  @apply relative rounded-lg shadow-md max-w-2xl;
  padding: 2rem 1.5rem !important;
}

.lno-select-field {
  @apply pr-8 bg-no-repeat appearance-none;
  background-size: 1.5rem;
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' fill='none' viewBox='0 0 20 20'%3e%3cpath stroke='%236b7280' stroke-linecap='round' stroke-linejoin='round' stroke-width='1.5' d='M6 8l4 4 4-4'/%3e%3c/svg%3e");
  background-position: calc(100% - 0.25rem) center;
}

.lno-nav--active {
  @apply border-b-4 border-t-4 border-lean-orange text-lean-orange;
  border-top-color: transparent;
}

.link-cover::before {
  content: " ";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

[class*="line-clamp-"] {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.line-clamp-1 {
  -webkit-line-clamp: 1;
}
.line-clamp-3 {
  -webkit-line-clamp: 3;
}

/* Categories dropdown accordion */
.lno-accordion {
  --active-bg: #fff;
}
.lno-accordion.lno-accordion--single {
  --active-bg: #f4f4f6;
}
.lno-accordion > li {
  position: relative;
  margin: 0 !important; /* override lib css */
}
.lno-accordion > li > button,
.lno-accordion__item-childless a {
  text-transform: uppercase;
  text-align: left;
}
.lno-accordion > li > button,
.lno-accordion__item-childless a,
.lno-accordion div[role="region"] a {
  position: relative;
  width: 100%;
  z-index: 1;
  border: none;
  border-bottom: 1px solid rgba(39, 44, 73, 0.1);
  padding-top: 0.625rem;
  padding-bottom: 0.625rem;
  padding-right: 0.5rem;
  padding-left: 1.5rem;
  font-size: 1.125rem;
  color: #7d8092;
}
.lno-accordion > li > button::before,
.lno-accordion__item-childless a::before,
.lno-accordion div[role="region"] a::before {
  transform: scaleX(1.25);
  transform-origin: left;
  width: 1.5rem;
  position: absolute;
  left: 0;
  z-index: 1;
}
.lno-accordion > li > button::before {
  content: "\203A";
}
.lno-accordion > li > button:hover,
.lno-accordion div[role="region"] a:hover {
  color: #00499d;
}
.lno-accordion > li > button[aria-expanded="true"] {
  color: #272c49;
}
.lno-accordion > li > button[aria-expanded="true"]::before {
  content: "\2304";
  transform: scaleX(1.75) scaleY(1.25) translateY(-2px);
}
.lno-accordion__item-childless a,
.lno-accordion div[role="region"] a {
  display: block;
}
.lno-accordion div[role="region"] {
  padding: 0;
  padding-left: 1.5rem;
}
.lno-accordion__item-active {
  position: relative;
}
.lno-accordion__item-childless a:focus,
.lno-accordion > li > button:focus {
  outline: none;
}
.lno-accordion__item-childless a:focus {
  background-color: var(--active-bg);
  box-shadow: -4px 0px 0 #fff, 4px 0px 0 #fff;
}
.lno-accordion > li > button:focus::after,
.lno-accordion__item-active::after {
  content: " ";
  position: absolute;
  left: -0.25rem;
  right: -0.25rem;
  top: 0;
  bottom: 0;
  border-radius: 0.5rem;
  background-color: var(--active-bg);
}
.lno-accordion div[role="region"] .lno-accordion__item-active::after {
  left: -1.75rem;
}

/* fix Nova WYSIWYG editor font styles */
.lno-wysiwyg-body p:not(:first-child) {
  margin-top: 1.5rem;
}
.lno-wysiwyg-body font,
.lno-wysiwyg-body span,
.lno-wysiwyg-body div {
  font-family: inherit !important;
  font-size: inherit !important;
  text-align: left !important;
  color: inherit !important;
}
.lno-wysiwyg-body * + br,
.lno-wysiwyg-body br:first-child,
.lno-wysiwyg-body br:last-child {
  display: none;
}
.lno-wysiwyg-body iframe,
.lno-wysiwyg-body img {
  max-width: 100%;
  margin: 2rem auto;
}
.lno-wysiwyg-body a:not(:hover) {
  text-decoration-color: #a9abb6 !important;
}
.lno-wysiwyg-body a {
  @apply text-lean-blue underline hover:text-lean-orange focus:outline-none focus:ring !important;
}

/* svelte-accessible-dialog */
[data-svelte-dialog-overlay] {
  z-index: 20;
  background-color: rgba(0, 0, 0, 0.5) !important;
}

/* [data-svelte-dialog-content] {} */
.lno-dialog-content--signup {
  width: calc(100vw - 2rem) !important;
  margin: 2rem auto !important;
}

/* svelte-social-buttons */
.ssbc-button {
  transition: 25ms ease-out;
  padding: 0.5rem 1rem;
  display: flex;
  align-items: center;
  font-size: 0.875rem;
  font-weight: 500;
}
.ssbc-button__link {
  color: #fff;
}
.ssbc-button svg {
  margin-right: 0.5rem;
  fill: currentColor;
  width: 1rem;
  height: 1rem;
}
.ssbc-button--email {
  background-color: #777777;
}
.ssbc-button--email:active,
.ssbc-button--email:hover {
  background-color: #5e5e5e;
}
.ssbc-button--linkedin {
  background-color: #0077b5;
}
.ssbc-button--linkedin:active,
.ssbc-button--linkedin:hover {
  background-color: #046293;
}
.ssbc-button--whatsapp {
  background-color: #25d366;
}
.ssbc-button--whatsapp:active,
.ssbc-button--whatsapp:hover {
  background-color: #1da851;
}
.ssbc-button--facebook {
  background-color: #3b5998;
}
.ssbc-button--facebook:active,
.ssbc-button--facebook:hover {
  background-color: #2d4373;
}
.ssbc-button--twitter {
  background-color: #55acee;
}
.ssbc-button--twitter:active,
.ssbc-button--twitter:hover {
  background-color: #2795e9;
}

@media (min-width: 640px) {
  /* Tailwind @apply cannot be used bcs it's overridden by the lib css. */
  .lno-dialog-content {
    padding: 3rem !important;
  }
}

@media (min-width: 1024px) {
  .lno-logo {
    width: 6.375rem;
    height: 2.8125rem;
  }
}
